@import '~normalize.css';
@import './fonts.scss';

body {
	background: $background;
	color: #ffffff;
}

.app {
	padding: 15px;
	display: flex;
	flex-direction: column;
	gap: 15px;
	margin: 0 auto;
	// max-width: fit-content;
	max-width: 600px;

	&__title {
		font-family: 'Bubbles';
		font-weight: 700;
		text-align: center;
		font-size: 58px;
	}

	.app-buttons {
		& button {
			border: none;
			border-radius: 5px;
			padding: 10px 15px;
			color: rgba(255, 255, 255, 0.5);
			background: rgb(61, 41, 99);
			cursor: pointer;

			&:hover {
				background: rgb(231, 4, 129);
				color: rgb(255, 255, 255);
			}
		}
	}

	.sound-active {
		text-decoration: line-through;
	}

	.app-info {
		display: flex;
		justify-content: center;
		gap: 10px;
		padding: 10px 15px;
		border-radius: 5px;
		background: rgb(61, 41, 99);
	}

	&__game {
		display: grid;
		// grid-template-rows: repeat(4, 1fr);
		// grid-template-columns: repeat(4, 1fr);
		// gap: 10px;
		user-select: none;
		margin: auto;
		// width: fit-content;
		width: 100%;
		aspect-ratio: 1/1;
		// @media (max-width:900px) {
		// 	gap: 8px;
		// }
		// @media (max-width:730px) {
		// 	gap: 6px;
		// }
		// @media (max-width:560px) {
		// 	gap: 4px;
		// }
		// @media (max-width:560px) {
		// 	gap: 3px;
		// }

		.game-item {
			border-radius: 5%;
			background: rgb(106, 198, 184);
			font-family: 'Bubbles';
			font-weight: 700;
			text-align: center;
			cursor: pointer;
			transition: scale .2s;
			// width: 100px;
			// height: 100px;
			// line-height: 100px;
			// font-size: 50px;
			// font-size: 20px;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			// @media (max-width:900px) {
			// 	width: 80px;
			// 	height: 80px;
			// 	font-size: 40px;
			// 	line-height: 80px;
			// 	border-radius: 8px;
			// }
			// @media (max-width:730px) {
			// 	width: 60px;
			// 	height: 60px;
			// 	font-size: 30px;
			// 	line-height: 60px;
			// 	border-radius: 6px;
			// }
			// @media (max-width:560px) {
			// 	width: 40px;
			// 	height: 40px;
			// 	font-size: 20px;
			// 	line-height: 40px;
			// 	border-radius: 4px;
			// }
			// @media (max-width:560px) {
			// 	width: 30px;
			// 	height: 30px;
			// 	font-size: 15px;
			// 	line-height: 30px;
			// 	border-radius: 3px;
			// }

			&_empty {
				background: rgb(61, 41, 99);
			}
		}
	}

	&__current-size {
		text-align: center;
	}

	&__sizes {
		text-align: center;
		cursor: pointer;
	}
}

.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.6);
	top: 0;
	left: 0;
	// opacity: 0;
	visibility: hidden;

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__content {
		background-color: #ffffff;
		color: #000000;
		max-width: 300px;
		padding: 20px;
		position: relative;
	}

	&__close {
		font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
		position: absolute;
		font-size: 24px;
		right: 5px;
		top: 2px;
		text-decoration-line: none;
		color: #000000;
	}

	&__text {
		font-size: 24px;
		// margin: 0 0 1em 0;
	}

	&__time,
	&__moves {
		color: red;
	}
}

.top-popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.6);
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;

	visibility: hidden;
	&__body {
		position: relative;
	}

	&__table {
		background-color: #ffffff;
		color: #000000;
		min-width: 320px;
		padding: 20px;
	}

	&__close {
		font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
		position: absolute;
		font-size: 24px;
		right: 5px;
		text-decoration-line: none;
		color: #000000;
	}

